.result {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding: 15rem 5.7rem 6rem;
  width: 100%;
  position: relative;

  @media (min-width: $stop-scaling) {
    padding: 150px 57px 60px;
  }

  @media (orientation: portrait) {
    padding: 8rem 0 4rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    padding: 8rem 0 3rem;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    padding: 3rem 0;
  }
}

.result__image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: -1;

  @media (orientation: portrait) {
    left: -1rem;
    right: -1rem;
  }

  picture {
    display: block;
    //width: 100%;
    //height: 100%;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.result__title {
  margin: 0 auto 2rem;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: 0 auto 20px;
  }

  @media (orientation: portrait) {
    margin-bottom: 1.5rem;
  }

  svg {
    display: block;
    width: 100%;
    height: 100%;
    stroke-width: 0.2rem;
    stroke-linejoin: round;
  }
}

.result__text {
  margin-bottom: 2rem;
  text-align: center;
  font-family: $font-alt;
  font-weight: 400;
  font-size: 4.8rem;
  line-height: 0.906;
  letter-spacing: -0.01em;

  @media (min-width: $stop-scaling) {
    margin-bottom: 20px;
    font-size: 48px;
  }

  @media (orientation: portrait) {
    font-size: 2.8rem;
    line-height: 1.1;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    font-size: 2.8rem;
  }

  p {
    margin: 0;
  }
}

.result__button {
  @include btn-reset;
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin: auto auto 1rem;
  min-width: 34rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  line-height: 1.2;
  color: $c-light;

  @media (min-width: $stop-scaling) {
    margin: auto auto 10px;
    min-width: 340px;
    font-size: 24px;
  }

  @media (orientation: portrait) {
    margin: auto 0 0;
    transform: none;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  @media (max-width: $tablet) and (orientation: landscape) {
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    margin: auto 0 0;
    width: 100%;
    min-width: auto;
    font-size: 1.6rem;
  }

  span {
    position: relative;
    margin-left: 1.5rem;
    width: 4rem;
    height: 4rem;
    color: $c-dark;

    @media (min-width: $stop-scaling) {
      margin-left: 15px;
      width: 40px;
      height: 40px;
    }

    &::before {
      @include center;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $c-light;
      border: 2px solid $c-light;
      border-radius: 50%;
      z-index: -1;
    }
  }

  svg {
    @include center;
    margin-left: 0.3rem;
    width: 2.4rem;
    height: 1.9rem;

    @media (min-width: $stop-scaling) {
      margin-left: 3px;
      width: 24px;
      height: 19px;
    }
  }

  &:hover {
    span::before {
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    outline: none;

    span::before {
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    span {
      color: $c-light;
    }

    span::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.result__form {
  margin-top: auto;
}

.result--trip,
.result--prize {
  .result__title {
    width: 57.3rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      width: 573px;
      height: 113px;
    }

    @media (orientation: portrait) {
      width: 28.7rem;
      height: 5.7rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      width: 40rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin-bottom: 0;
      width: 35rem;
    }
  }

  .image {
    position: absolute;
    width: auto;
    height: auto;
  }

  .image-leaf1 {
    top: 30%;
    right: 15%;
    bottom: 30%;
  }

  .image-leaf2 {
    top: 45%;
    bottom: 15%;
    right: 55%;
  }

  .image-leaf-green {
    top: 60%;
    left: 57%;
    bottom: 24%;
  }

  .image-suitcase {
    top: 25%;
    bottom: 10%;
  }

  .image-flamingo {
    top: 40%;
    bottom: 15%;
    left: 25%;
  }

  .form__field {
    width: 6rem;

    &::placeholder {
      color: transparent;
      opacity: 0.5;
      transition: color 0.5s ease-in 2s;
    }
  }

  .form__field, .form__button {
    opacity: 0;
    transition: opacity 0.3s ease-in-out 2s, width 0.3s ease-in 2s;
  }

  .screen--show & {
    .image-flamingo {
      animation:
        animation-flamingo 0.5s ease-out,
        animation-flamingo-rotate 2s ease-out,
        animation-flamingo-infinite 2s 3s infinite alternate;
    }

    .image-suitcase {
      animation: animation-suitcase 0.6s ease-in-out;
      animation-fill-mode: forwards;
    }

    .image-leaf1 {
      animation: animation-leaf1 3s ease-out;
    }

    .image-leaf2 {
      animation: animation-leaf2 3s ease-out;
    }

    .image-leaf-green {
      animation: animation-leaf-green 3s ease-out;
    }

    .form__field, .form__button {
      opacity: 1;
    }

    .form__field {
      width: calc(100% - 31rem);

      &::placeholder {
        color: unset;
      }
    }
  }
}

.result--negative {
  .result__title {
    margin-top: 9.5rem;
    width: 66.5rem;
    height: 11.3rem;

    @media (min-width: $stop-scaling) {
      margin-top: 95px;
      width: 665px;
      height: 113px;
    }

    @media (orientation: portrait) {
      margin-top: 0;
      width: 28.8rem;
      height: 4.9rem;
    }

    @media (max-width: $tablet) and (orientation: landscape) {
      margin-top: 0;
      width: 50rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      width: 45rem;
    }
  }

  .result__button {
    .screen--show & {
      animation: showButton 0.4s ease-in-out 0.3s;
      animation-fill-mode: both;

      span {
        animation: moveRotate 0.4s ease-in-out 0.3s;
      }
    }
  }
}

@keyframes animation-flamingo {
  from {
    top: 80%;
    bottom: -25%;
    opacity: 0;
  }

  to {
    top: 40%;
    bottom: 15%;
    opacity: 1;
  }
}

@keyframes animation-flamingo-rotate {
  from {
    transform: rotate(-25deg);
  }
  50% {
    transform: rotate(-10deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@keyframes animation-flamingo-infinite {
  from {
    transform: translateY(0vh);
  }
  to {
    transform: translateY(1vh);
  }
}

@keyframes animation-suitcase {
  from {
    transform: translateY(16vh);
    opacity: 0;
  }

  70% {
    transform: translateY(-2vh);
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes animation-leaf1 {
  from {
    transform: rotate(-60deg);
  }
  20% {
    transform: rotate(10deg);
  }
  35% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(2deg);
  }
  70% {
    transform: rotate(-2deg);
  }
  85% {
    transform: rotate(1deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes animation-leaf2 {
  0% {
    transform: rotate(75deg);
  }
  25% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(5deg);
  }
  55% {
    transform: rotate(-2deg);
  }
  70% {
    transform: rotate(2deg);
  }
  80% {
    transform: rotate(-1deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes leaf_img {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes animation-leaf-green {
  from {
    transform: rotate(-60deg);
  }
  25% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(-5deg);
  }
  55% {
    transform: rotate(2deg);
  }
  70% {
    transform: rotate(-2deg);
  }
  85% {
    transform: rotate(1deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes showButton {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes moveRotate {
  from {
    transform: translateX(4rem) rotate(180deg);
  }
  to {
    transform: none;
  }
}
